import MeetupList from "../meetups/MeetupList"
import './pages.css'
import { useState, useEffect } from "react"
import Modal from "../Modal"

const AllMeetups = () => {

    const [isLoading, setIsLoading] = useState(true);
    const [loadedMeetups, setLoadedMeetups] = useState([])

    useEffect(() => {
        setIsLoading(true);
        fetch(
            'https://react-test-app-1bb75-default-rtdb.firebaseio.com/meetups.json'
        ).then((response) => {
            return response.json();
        }).then(data => {
            const meetups = [];

            for (const key in data) {
                const meetup = {
                    id: key,
                    ...data[key]
                };
                meetups.push(meetup)
            }

            setIsLoading(false)
            setLoadedMeetups(meetups)
        });
    }, []);

    const [modalShow, setModalShow] = useState(false)

    function modalHandler() {
        setModalShow(true)
        console.log('show modal')
    }


    if (isLoading) {
        return (
            <div>Loading...</div>
        )
    }

    console.log(modalShow);

    function closeModalHandler() {
        setModalShow(false)
        console.log('show modal')
    }

    return (
        <div>
            {modalShow && <Modal onClose={closeModalHandler} />}
            <h1>All Meetups</h1>
            <MeetupList meetups={loadedMeetups} onShow={modalHandler} />
        </div>
    )
}

export default AllMeetups
