import classes from './MeetupItem.module.css'
import Card from '../ui/Card'
import FavouritesContext from '../../store/favourites-context'
import { useContext } from 'react'

const MeetupItem = (props) => {
    const favouritesCtx = useContext(FavouritesContext);

    const itemIsFavourite = favouritesCtx.itemIsFavourite(props.id)

    function toggleFavouritesHandler() {
        if (itemIsFavourite) {
            favouritesCtx.removeFavourite(props.id);
        } else {
            favouritesCtx.addFavourite({
                id: props.id,
                title: props.title,
                description: props.description,
                image: props.image,
                address: props.address,
                date: props.date
            })
        }
    }

    function showModalHandler() {
        props.onShowModal();
    }

    return (
        <li className={classes.item} >
            <Card>
                <div>
                    <img src={props.image} alt={props.title} />
                </div>
                <div className={classes.details}>
                    <h2>{props.title}</h2>
                    <address>{props.address}</address>
                    <h6>{props.date}</h6>
                    <p>{props.description}</p>
                    <div className={classes.action}>
                        <button onClick={showModalHandler}>Attend</button>
                        <button onClick={toggleFavouritesHandler}>{itemIsFavourite ? 'Unfavourite' : 'Favourite'}</button>
                    </div>
                </div>
                <div className={classes.buttons}>
                    <button className={classes.delete}>X</button>
                </div>
            </Card>
        </li>
    )
}

export default MeetupItem
